body{
      transition: 0.1s linear;
}

/*---------HEADER----------*/
.header{
  width: 100%;
  height: 100%;
  position: relative;
}

.header__container__logo{
  align-items: center;
  max-width: 50px;
  max-height: 50px;
}

.header__continer{
  width: 100%;
  height: 100%;
  max-width: 80rem;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 0 auto;
  padding-top: 25px;
}

.logo{
  max-width: 50px;
  max-height: 50px;
}

.header__container__nav{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 50px;
}

.nav__list{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 25px;
}

.nav__list__item{
  list-style: none;
}

.nav__list__item-link {
  position: relative;
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
}

.nav__list__item-link {
  position: relative;
  text-decoration: none;
  color: inherit;
  padding-bottom: 4px; 
}

.nav__list__item-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0%;
  height: 2px;
  background-color: black; 
  transition: width 0.3s ease-in-out;
}

.nav__list__item-link:hover::after {
  width: 100%;
}

.nav__buttons{
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
}

.nav__button__download{
  padding: 10px 15px 10px 15px;
  background-color: #000;
  color: #fff;
  border-radius: 12px;
}

body.dark-theme {
  background-color: #121212;
  color: white;
}


.theme-toggle {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  color: inherit;
  padding: 5px;
  transition: 0.5s linear;
  width: 25px;
}

.theme-toggle:hover {
  transform: scale(1.1);
}


/*----------HERO----------*/

.section{
  width: 100%;
  height: 100%;
}

.section__container{
    max-width: 80rem;
    padding-top: 100px;
    margin: 0 auto;
}

.section__container__inner{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: start;
}

.hero__title{
  font-size: 3.75rem;
  margin: 0;
}

.grey{
  color: #4B5563;
}

.left__block, .right__block{
  max-width: 50%;
  display: flex;
  flex-direction: column;
}

.hero__image{
  max-width: 17.5rem;
  max-height: 20rem;
  object-fit: cover;
  box-shadow: 10px 5px 5px #4B5563;
  border-radius: 2px;
}

.row{
  display: flex;
  flex-direction: column;
}

.icons__container{
  display: flex;
  align-items: center;
  gap: 10px;
}

.icon-link{
  color: #000;
  text-decoration: none;
  transition: 0.2s linear;
}

.icon-link:hover{
  color: blue;
}

body.dark-theme .icon {
  color: #fff;
}

body.dark-theme .grey {
  color: #fff;
}

/* ABOUT ME SECTION */

.section__heading__container{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section__title{
  color: #4B5563;
  border-radius: 12px;
  padding: 10px 20px;
  border: 1px #4B5563;
  background: #E5E7EB;
  font-size: 0.875rem;
  max-width: 100px;
}

.section__description{
  font-size: 1.25rem;
}

.section__image{
  max-width: 20rem;
  max-height: 24rem;
  object-fit: cover;
  box-shadow: 10px 5px 5px #4B5563;
  border-radius: 2px;
}


/* TOOLS SECTION */

.grid__container {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    gap: 90px;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    text-align: center;
  };

  .grid__container__item{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .item__image__box{
    width: 64px;
    height: 64px;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f5f5f5;
    border-radius: 8px;
    overflow: hidden;
  }

  .item__image__box-image{
    width: 48px;
    height: 48px;
    object-fit: contain;
  };
  
.item__image__box-text{
    font-size: '14px';
    text-align: 'center';
    color: '#374151';
    font-weight: '500';
}